'use client';

import Image from 'next/image';
import Link from 'next/link';

export default function BlogPage({ allPosts = [] }) {
  return (
    <div className="max-w-6xl mx-auto py-12 px-4 sm:px-6 lg:px-8">
      <h1 className="text-4xl font-bold text-gray-900 mb-10 text-center">Latest Blog Posts</h1>

      {allPosts.length === 0 ? (
        <p className="text-center text-gray-500">No blog posts available yet.</p>
      ) : (
        <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
          {allPosts.map((post) => (
            <Link
              href={`/blog/${post.slug}`}
              key={post.slug}
              className="group border rounded-xl overflow-hidden shadow hover:shadow-lg transition-all duration-300 bg-white"
            >
              <div className="relative w-full h-48">
                <Image
                  src={post.image || '/images/default.jpg'}
                  alt={post.title}
                  fill
                  className="object-cover group-hover:scale-105 transition-transform duration-300"
                />
              </div>
              <div className="p-5">
                <h2 className="text-xl font-semibold text-gray-800 group-hover:text-blue-600">
                  {post.title}
                </h2>
                <p className="text-sm text-gray-500 mt-1">
                  {new Date(post.date).toLocaleDateString()} â€¢ {post.readingTime}
                </p>
                <p className="mt-3 text-gray-600">{post.description}</p>

                <div className="mt-4 flex items-center gap-3">
                  <Image
                    src={post.author.avatar || '/images/profile.png'}
                    alt={post.author.name}
                    width={32}
                    height={32}
                    className="rounded-full object-cover"
                  />
                  <span className="text-sm text-gray-700">{post.author.name}</span>
                </div>
              </div>
            </Link>
          ))}
        </div>
      )}
    </div>
  );
}
